:root {
  --header-height: 100px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  height: 100%;
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 24px;
  line-height: 1.17;
  color: #000;
  background-color: #fff;
}

img,
video {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

@media screen and (max-width: 1024px) {
  body {
    font-size: 20px;
  }
}
@media screen and (max-width: 768px) {
  body {
    font-size: 18px;
  }
}
@media screen and (max-width: 575px) {
  body {
    font-size: 16px;
  }
}
@media screen and (max-width: 1024px) {
  .menu {
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 8;
    display: none;
    flex-direction: column;
    align-items: center;
    overflow: auto;
    padding: 50px 15px;
    width: 100%;
    max-width: 350px;
    background: #fff;
    opacity: 0;
    transition: 0.35s ease-in-out;
  }
  .menu.is-active {
    opacity: 1;
  }
  .overlay {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 5;
    display: none;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: 0.35s ease-in-out;
  }
  .overlay.is-active {
    opacity: 1;
  }
}
.nav {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 16px;
}
.nav li a:hover {
  text-decoration: underline;
}
.nav li a.is-active {
  color: #063a8d;
}

.title {
  font-family: "Georgia", "Verdana", serif;
  font-weight: 700;
  font-size: 64px;
}

.subtitle {
  font-family: "Georgia", "Verdana", serif;
  font-weight: 700;
  font-size: 48px;
}

.color-accent {
  color: #063a8d;
}

.uppercase {
  text-transform: uppercase;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.main {
  overflow-x: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1140px;
}

.text-center {
  text-align: center;
}

.disable-scroll,
.overflow-hidden {
  overflow: hidden;
}
.disable-scroll body,
.overflow-hidden body {
  overflow: hidden;
}

@media screen and (max-width: 1280px) {
  .title {
    font-size: 48px;
  }
  .subtitle {
    font-size: 36px;
  }
}
@media screen and (max-width: 1024px) {
  .title {
    font-size: 42px;
  }
  .subtitle {
    font-size: 30px;
  }
}
@media screen and (max-width: 768px) {
  .title {
    font-size: 36px;
  }
  .subtitle {
    font-size: 24px;
  }
}
.burger {
  position: relative;
  display: none;
  border: none;
  padding: 0;
  width: 26px;
  height: 20px;
  color: #063a8d;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: currentcolor;
  transition: transform 0.35s ease-in-out, top 0.35s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - 3px);
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 3px;
  background-color: currentcolor;
  transform: translateY(-50%);
  transition: transform 0.35s ease-in-out;
}
.burger.is-active {
  z-index: 9;
}
.burger.is-active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.is-active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.is-active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

@media screen and (max-width: 1024px) {
  .burger {
    display: block;
  }
}
.socials {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: -8px;
}
.socials li {
  margin: 8px;
  transition: 0.25s ease-in-out;
}
.socials li:hover {
  transform: scale(1.05);
}
.socials a {
  display: flex;
}

@media screen and (max-width: 1024px) {
  .socials {
    justify-content: center;
  }
}
@media screen and (max-width: 768px) {
  .socials li img {
    width: 30px;
    height: 30px;
  }
}
.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  padding: 20px 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: flex;
}
.header__nav ul {
  display: flex;
}
.header__nav li {
  margin-right: 10px;
}
.header__nav li:last-child {
  margin-right: 0;
}

@media screen and (max-width: 1024px) {
  .header {
    padding: 10px 0;
  }
  .header__logo img {
    width: 160px;
  }
  .header__nav ul {
    display: block;
  }
  .header__nav li {
    margin-right: 0;
    text-align: center;
  }
  .header__nav li:last-child {
    margin-right: 0;
  }
  .header__nav a {
    display: block;
    padding: 12px 0;
    font-size: 20px;
  }
}
.hero {
  padding: 500px 0 30px;
}
.hero__content {
  position: relative;
  margin: 0 auto;
  margin-bottom: 80px;
  border-radius: 10px;
  padding: 60px 30px;
  width: 100%;
  max-width: 730px;
  text-align: center;
  color: #fff;
  background-color: #063a8d;
}
.hero__content::before {
  content: "";
  position: absolute;
  left: -190px;
  bottom: 97%;
  width: 705px;
  height: 330px;
  background: url("../img/hero-1.png") center/cover no-repeat;
}
.hero__content::after {
  content: "";
  position: absolute;
  right: -211px;
  top: 50%;
  width: 487px;
  height: 322px;
  background: url("../img/hero-2.png") center/cover no-repeat;
  transform: translateY(-50%);
}
.hero__content p {
  position: relative;
  z-index: 2;
}
.hero__title {
  position: relative;
  z-index: 2;
  margin-bottom: 25px;
  line-height: 1;
  text-transform: uppercase;
}

@media screen and (max-width: 1024px) {
  .hero {
    padding-top: 400px;
  }
  .hero__content {
    margin-bottom: 40px;
    margin-left: 0;
  }
}
@media screen and (max-width: 768px) {
  .hero {
    padding-top: 350px;
  }
  .hero__title {
    margin-bottom: 10px;
  }
  .hero__content::before {
    left: -10px;
    bottom: 122%;
    width: 365px;
    height: 170px;
  }
  .hero__content::after {
    right: 0;
    top: auto;
    bottom: 27%;
    width: 200px;
    height: 192px;
    background-image: url("../img/hero-2-mobile.png");
  }
}
@media screen and (max-width: 575px) {
  .hero {
    padding-top: 220px;
  }
  .hero__content {
    padding: 37px 15px;
  }
  .hero__content::before {
    left: -10px;
    bottom: 170px;
    width: 230px;
    height: 108px;
  }
  .hero__content::after {
    bottom: 50px;
    width: 125px;
    height: 122px;
  }
}
.about__inner {
  position: relative;
  padding: 45px 0;
}
.about__img {
  min-height: 300px;
}
.about__img picture {
  display: flex;
  justify-content: center;
}
.about__content {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  border-radius: 10px;
  padding: 60px 20px 20px;
  width: 100%;
  max-width: 730px;
  text-align: center;
  color: #000;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(25px);
}
.about__title {
  margin-bottom: 15px;
}

@media screen and (max-width: 768px) {
  .about__img {
    min-height: auto;
  }
  .about__content {
    position: static;
  }
}
.info {
  padding: 80px 0 90px;
  color: #fff;
}
.info__inner {
  position: relative;
  border-radius: 10px;
  padding: 60px 20px;
  max-width: 730px;
  text-align: center;
  background-color: #063a8d;
}
.info__title {
  margin-bottom: 18px;
}
.info__img {
  position: absolute;
  right: calc(-100% + 240px);
  top: -60px;
}
.info__img picture {
  display: flex;
}

@media screen and (max-width: 1024px) {
  .info {
    padding-top: 100px;
  }
  .info__inner {
    margin: 0 auto;
  }
  .info__img {
    right: -120px;
    top: -154px;
  }
  .info__img img {
    width: 300px;
  }
}
@media screen and (max-width: 768px) {
  .info {
    position: relative;
    padding-top: 300px;
    padding-bottom: 40px;
  }
  .info__inner {
    position: static;
  }
  .info__img {
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
  }
  .info__img picture {
    justify-content: center;
  }
}
.images {
  padding: 60px 0 80px;
}
.images__title {
  margin-bottom: 70px;
  text-align: center;
}
.images__slider {
  margin: 0 auto;
  padding-top: 115px;
  max-width: 800px;
  max-height: 500px;
}
.images__item {
  position: relative;
  padding-bottom: 90px;
  cursor: pointer;
}
.images__item picture {
  display: flex;
  transform-origin: bottom;
  transition: transform 0.25s ease-in-out;
}
.images__item img {
  border-radius: 10px;
}
.images__item.swiper-slide-active .images__pagination {
  background-color: #063a8d;
}
.images__item.swiper-slide-active picture {
  transform: scale(1.32) translateY(-10px);
}
.images__pagination {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  border-radius: 5px;
  width: 90px;
  height: 10px;
  background-color: rgba(6, 58, 141, 0.56);
  transition: background-color 0.25s ease-in-out;
}

@media screen and (max-width: 768px) {
  .images {
    padding-top: 30px;
  }
  .images__item {
    padding-bottom: 40px;
  }
}
@media screen and (max-width: 475px) {
  .images__title {
    margin-bottom: 40px;
  }
  .images__slider {
    margin: 0 -15px;
  }
  .images__pagination {
    width: 70px;
  }
}
.footer {
  padding: 40px 0 35px;
  color: #fff;
  background-color: #063a8d;
}
.footer__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.footer__link {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 16px;
  text-transform: uppercase;
}
.footer__link:hover {
  text-decoration: underline;
}
.footer__img {
  margin-bottom: 40px;
}
.footer__nav {
  margin-bottom: 15px;
}
.footer__nav li {
  margin-bottom: 15px;
}
.footer__nav li a.is-active {
  color: #fff;
}
.footer__nav li:last-child {
  margin-bottom: 0;
}
.footer .socials {
  justify-content: center;
}

@media screen and (max-width: 575px) {
  .footer__img {
    margin-bottom: 20px;
  }
  .footer__img img {
    width: 160px;
  }
}